import React, {useState} from 'react'


function Card(props){

const [isMouseOver,setMouseOver] = useState(false);

  function handleHover(){
    setMouseOver(true);
  }

  function handleOut(){
    setMouseOver(false);
  }
  return (
          <>
            <div className="col-sm-6 col-md-4 col-lg-3 mt-4" >
                <div className="card card-inverse card-primary" style={{height:""+props.cardSize+""}}>
                    <img className="card-img-top"
                     src={props.Img?props.Img:"https://i.redd.it/fr3ij4z9gti01.jpg"} 
                     onMouseOver={handleHover}
                     onMouseOut={handleOut}
                     style={{transform:isMouseOver?'scale(1.1)':null,height:"200px"}} />
                    <blockquote className="card-blockquote p-3">
                    <h5 className="card-title">{props.Title}</h5>
                        <p>{props.Desc}</p>
                    </blockquote>
                    <div className="card-footer" style={{marginTop:"auto", textAlign:"center"}}>
                        <small className="text-muted">
                        <a  href={props.Url}>Go to News Url</a>
                        </small>
                        </div>
                </div>
            </div>
            </>

  );
}
export default Card;
