import React, { useState } from 'react';
import {NavLink, useHistory} from 'react-router-dom';

function Navbar(){

const [searchData,setSearch] = useState({search: ""});
const History = useHistory();

let name,value;
function handleSearch(event){
name = event.target.name;
value = event.target.value;
setSearch({...searchData,[name]:value});
}

function searchPost(event){
  event.preventDefault();
const {search} = searchData;
History.push("/category/"+search);
}

return(
<div>
<div id="Top_container">
    <nav className="navbar navbar-expand-lg  navbar-dark bg-dark" style={{padding:'40px 0px' }}>
  <div className="container-fluid">
    <NavLink className="navbar-brand blog-header-logo fs-1" to="/"><i className="far fa-newspaper"> NewsNow</i></NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <form className="d-flex mx-auto">
        <input className="form-control me-4" type="search" name="search" value={searchData.search} onChange={handleSearch} placeholder="Search" aria-label="Search" style={{width:'600px'}}/>
        <button className="btn btn-outline-success" type="submit" onClick={searchPost}>Search</button>
      </form>
      <ul className="navbar-nav  me-auto mb-2 mb-lg-0">
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          <i className="fas fa-list-alt">  Category</i>
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a className="dropdown-item"  href="/category/sport"><i className="fas fa-baseball-ball"> Sports</i></a></li>
            <li><a className="dropdown-item" href="/category/health"><i className="fas fa-first-aid"> Heaths</i></a></li>
            <li><a className="dropdown-item" href="/category/general"><i className="fas fa-poll"> General</i></a></li>
            <li><a className="dropdown-item" href="/category/entertainment"><i className="fas fa-tv"> Entertainment</i></a></li>
            <li><a className="dropdown-item" href="/category/technology"><i className="fas fa-cogs"> Technology</i></a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><NavLink className="dropdown-item" to="/category/covid"><i className="fas fa-virus"> Covid</i></NavLink></li>
          </ul>
        </li>

        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          <i className="fas fa-user-circle"> Member</i>
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li> <NavLink className="dropdown-item" to="/register"><i className="fas fa-user-circle">  Membership</i></NavLink></li>
            <li><hr className="dropdown-divider"/></li>
            <li><NavLink className="dropdown-item" to="/dashboard"><i className="fas fa-user-circle"> Dashboard</i></NavLink></li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</nav>
  </div>
</div>
)
}

export default Navbar;