import React, { useState } from 'react';
import './register.css';
import {NavLink , useHistory} from 'react-router-dom';

function Register(){

    const history = useHistory();

    const [user, setUser] = useState({
        username: "",
        email: "",
        password: "",
        confirm_password: ""
    });

    let name,value;

    function handleInput(event){
       
        name=event.target.name;
        value=event.target.value;

        setUser({...user,[name]:value});
    }

   const postData = async (e) => {
        e.preventDefault();
        const { username , email , password , confirm_password } = user;

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json; charset=utf-8'},
            body: JSON.stringify(user)
        };
        
        const res = await fetch('https://newsnow85.herokuapp.com/register',requestOptions)

        const data = await res.json();
         history.push("/login")
    }

    return (
       <div className="body">
           <div className="signup-form">
    <form  method="POST">
		<h2>Create an Account</h2>
		<p className="hint-text">Sign up with your social media account or email address</p>
		{/* <div className="social-btn text-center">
			<NavLink to="#" className="btn btn-primary btn-lg"><i className="fa fa-facebook"></i> Facebook</NavLink>
			<NavLink to="#" className="btn btn-danger btn-lg"><i className="fa fa-google"></i> Google</NavLink>
		</div>
		<div className="or-seperator"><b>or</b></div> */}
        <div className="form-group">
        	<input type="text" className="form-control input-lg" name="username" value={user.username} onChange={handleInput} placeholder="Username" required="required"/>
        </div>
		<div className="form-group">
        	<input type="email" className="form-control input-lg" name="email" value={user.email} onChange={handleInput} placeholder="Email Address" required="required"/>
        </div>
		<div className="form-group">
            <input type="password" className="form-control input-lg" name="password" value={user.password} onChange={handleInput} placeholder="Password" required="required"/>
        </div>
		<div className="form-group">
            <input type="password" className="form-control input-lg" name="confirm_password" value={user.confirm_password} onChange={handleInput} placeholder="Confirm Password" required="required"/>
        </div>
        <div className="form-group">
            <input type="submit" value="Register" className="btn btn-success btn-lg btn-block signup-btn" onClick={postData}/>
        </div>
    </form>
    <div className="text-center">Already have an account? <NavLink to="/login">Login here</NavLink></div>
    <div className="text-center"><NavLink to="/">Back To Main Page</NavLink></div>
</div>
       </div> 
    )
}
export default Register;
