import React, { useEffect, useState } from 'react'
import './dashboard.css'
import {NavLink} from 'react-router-dom';
import Categorynews from './Categorynews'
import { useHistory } from 'react-router'
import LowerBoard from './LowerBoard'


function Dashboard(){

const history = useHistory();
const [userData,setUserData] = useState({});
const [Query, setQuery] = useState({
username:"",
  title:"",
  message:""
});
// ---------------------------------------------HANDLE CHANGE---------------------------------------------
let name,value;
function handleChange(event){
 name = event.target.name;
 value = event.target.value;
setQuery({...Query,[name]:value});
}
// -------------------------------------CALL DASHBOARD FOR FETCHING USER PROFILE---------------------------------------------
async function callDashboard(){
  try{
    const res = await fetch('https://newsnow85.herokuapp.com/dashboard',{
      method: "GET",
      credentials:"include",
      headers:{
        "Content-Type" : "application/json" 
      },
    });

    const data = await res.json();
    console.log(data);
    setUserData(data);
    
    if(!res.status === 200)
    throw res.error;
  }
  catch (err){
    history.push('/login')
    console.log(err);
  }
}
// ---------------------------------------------HANDLE CLICK FOR SUBMIT---------------------------------------------
async function handleClick(e){
e.preventDefault();

const requestOptions = {
  method: 'POST',
  headers: { 'Content-Type': 'application/json; charset=utf-8'},
  body: JSON.stringify(Query)
};

const res = await fetch('https://newsnow85.herokuapp.com/dashboard',requestOptions)

const data = await res.json();

if(!data)
console.log("Error Message not send");
else{
alert("Message send successfully");
setQuery({...Query,username:"",title:"",message:"",})}
}
// ---------------------------------------------USE EFFECT FUNCTION---------------------------------------------
useEffect(() => {
 console.log(" dashboard called")
callDashboard();
},[])

    return(
       <>
        <div className="container">
  <header className="blog-header py-3">
    <div className="row flex-nowrap justify-content-between align-items-center">
      <div className="col-4 ml-auto text-center heading">
        <a className="blog-header-logo text-dark fs-1" href="/"><i class="far fa-newspaper"> NewsNow</i></a>
      </div>
      <div className="col-4 d-flex justify-content-end align-items-center">
          <ul class="navbar-nav  me-auto mb-2 mb-lg-0">
        <li class="nav-item dropdown">
          <button type="button" class="btn btn-outline-secondary dropdown-toggle" style={{marginLeft:'300px'}} id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          <i class="fas fa-user-circle"> {userData.username}</i>
          </button>
          <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a class="dropdown-item" href="/covid"><i class="fas fa-virus"> Covid</i></a></li>
            <li><hr class="dropdown-divider"/></li>
            <li><NavLink class="dropdown-item" to="/logout"><i class="fas fa-sign-out-alt"> Sign Out</i></NavLink></li>
          </ul>
        </li>
      </ul>
      </div>
    </div>
  </header>

  <div className="nav-scroller py-1 mb-2">
    <nav className="nav d-flex justify-content-between">
      <a className="p-2 link-secondary" href="/category/sport"><i class="fas fa-baseball-ball">  Sports</i></a>
      <a className="p-2 link-secondary" href="/category/health"><i class="fas fa-first-aid">  Heaths</i></a>
      <a className="p-2 link-secondary" href="/category/general"><i class="fas fa-poll">  General</i></a>
      <a className="p-2 link-secondary" href="/category/entertainment"><i class="fas fa-tv">  Entertainment</i></a>
      <a className="p-2 link-secondary" href="/category/technology"><i class="fas fa-cogs">  Technology</i></a>
    </nav>
  </div>
</div>

<main className="container">
  <div className="p-4 p-md-5 mb-4 text-white rounded bg-dark">
    <div className="col-md-6 px-0">
      <h1 className="display-4 fst-italic">Welcome to NewsNow get various live Update news on various current affairs</h1>
      <p className="lead my-3">Multiple lines of text that form the lede, informing new readers quickly and efficiently about what’s most interesting in this post’s contents.</p>
      <p className="lead mb-0"><a href="#" className="text-white fw-bold">Continue reading...</a></p>
    </div>
  </div>

  <div className="row mb-2">
    <div className="col-md-6">
      <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
        <div className="col p-4 d-flex flex-column position-static">
          <strong className="d-inline-block mb-2 text-success">Magazines</strong>
          <h3 className="mb-0">Post title</h3>
          <div className="mb-1 text-muted">Nov 11</div>
          <p className="mb-auto">This is a wider card with supporting text below as a natural lead-in to additional content.</p>
        </div>
        <div className="col-auto d-none d-lg-block">
          <svg className="bd-placeholder-img" width="200" height="250" xmlns="http://www.w3.org/2000/svg" role="img" aria-label="Placeholder: Thumbnail" preserveAspectRatio="xMidYMid slice" focusable="false"><title>Placeholder</title><rect width="100%" height="100%" fill="#55595c"/><text x="50%" y="50%" fill="#eceeef" dy=".3em">Thumbnail</text></svg>
        </div>
      </div>
    </div>
    <div className="col-md-6">
      <form method="POST">
      <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
        <div className="col p-4 d-flex flex-column position-static" style={{height:'250px'}}>
        <strong className="d-inline-block mb-2 text-success">Query Box </strong>
        <div class="mb-3">
        <input type="text" name="username" onChange={handleChange} value={Query.username} placeholder="Enter your Username"/>
         <button type="submit"  onClick={handleClick} class="btn btn-outline-warning btn-sm btn float-right">Send</button>
        </div>
        <div class="mb-3">
        <input type="text" name="title" class="form-control" id="exampleFormControlInput1" onChange={handleChange} value={Query.title} placeholder="Enter Title"/>
      </div>
      <div class="mb-3">
        <textarea class="form-control" name="message" id="exampleFormControlTextarea1" onChange={handleChange} value={Query.message} placeholder="Type Your Query Here..." rows="3"></textarea>
      </div>
        </div>
      </div>
      </form>
    </div>
  </div>

  <div className="row g-5">
    <div className="col-md-8">
      <h3 className="pb-4 mb-4 fst-italic border-bottom">
        Fired Up with Latest Updates
      </h3>
      <div className="pb-4 mb-4 fst-italic border-bottom">
      <Categorynews Type="general" Image="0" flag="true"/>
      </div>

      <article className="blog-post">
        <h2 className="blog-post-title">New feature</h2>
        <p className="blog-post-meta">December 14, 2020 by <a href="#">Chris</a></p>

        <p>This is some additional paragraph placeholder content. It has been written to fill the available space and show how a longer snippet of text affects the surrounding content. We'll repeat it often to keep the demonstration flowing, so be on the lookout for this exact same string of text.</p>
        <ul>
          <li>First list item</li>
          <li>Second list item with a longer description</li>
          <li>Third list item to close it out</li>
        </ul>
        <p>This is some additional paragraph placeholder content. It's a slightly shorter version of the other highly repetitive body text used throughout.</p>
      </article>

    </div>

    <div className="col-md-4">
      <div className="position-sticky" style={{top: '2rem'}}>
        <div className="p-4 mb-3 bg-light rounded">
         <h4 className="fst-italic">About</h4>
          <p className="mb-0">In this Covid pandemic We provide our best to get you updated with all the current news. You can also contribute us by posting some incident which might be benificals for the people.</p>
        </div>

        <div className="p-4">
          <h4 className="fst-italic">Category</h4>
          <ol className="list-unstyled mb-0">
          <li><a  href="/category/sports">Sports</a></li>
            <li><a  href="/category/health">Healths</a></li>
            <li><a  href="/category/general">General</a></li>
            <li><a  href="/category/entertainment">Entertainment</a></li>
            <li><a  href="/category/technology">Technology</a></li>
          </ol>
        </div>

        <div className="p-4">
          <h4 className="fst-italic">Elsewhere</h4>
          <ol className="list-unstyled">
            <li><a href="#">GitHub</a></li>
            <li><a href="#">Twitter</a></li>
            <li><a href="#">Facebook</a></li>
          </ol>
        </div>
      </div>
    </div> 
  </div>
</main>
<LowerBoard/>

       </>
    )
}

export default Dashboard;