import React from 'react';
import Navbar from './Navbar';

const customStyles = {
  backgroundColor: '#f5ba13',
  textAlign: 'center',
  color:'white',
  marginBottom:'0px',
  padding:'40px'
};

const customStyles1 = {
  backgroundColor: 'black',
  textAlign: 'center',
  color:'white',
  marginBottom:'0px',
  padding:'40px'
};

function TopBoard(){
  return(
    <div className="NewsUpdate">
    <Navbar/>
    <div className="row">
    <div className="col-2" style={customStyles1} >Welcome to NewsNow</div>
    <div className="col-10" style={customStyles}>"Good news is rare these days, every glittering ounce of it should be cherishd hoarded and worshipped and fondled like a precious diamond" - [ Hunter S.Thomas ]</div>
    </div>
</div>
  );
}
export default TopBoard;
