import React, { useEffect , useState} from 'react';
import TopBoard from './TopBoard';
function Covid(){

    const [covidData , setCovidData] = useState([]);

    const url = 'https://api.rootnet.in/covid19-in/stats/latest';

    const getCovidDetails = async() => {
    const response = await fetch(url);
    setCovidData((await response.json()).data.regional);}

useEffect(()=>{
    getCovidDetails();
},[])

return (
    <>
    <TopBoard />

    <div id="carouselExampleCaptions" class="carousel slide" data-bs-ride="carousel">
  <div class="carousel-indicators">
    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1" aria-label="Slide 2"></button>
  </div>
  <div class="carousel-inner">
    <div class="carousel-item active" data-bs-interval="10000">
      <img src="/images/VDH-Shareable_How-Does-It-Feel-To-Get-The-Covid-19-Vaccine_English.jpg" style={{height:'300px'}} class="d-block w-100" />
      <div class="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div class="carousel-item" data-bs-interval="10000">
      <img src="/images/COVID-resource.jpg" class="d-block w-100" style={{height:'300px'}} />
      <div class="carousel-caption d-none d-md-block">
      </div>
    </div>
  </div>
  <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Previous</span>
  </button>
  <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
    <span class="carousel-control-next-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Next</span>
  </button>
</div>

    <div>
    <h1 style={{textAlign:'center', margin:'20px',color:'#fd7e14'}}>Latest Covid-19 Live Update In INDIA</h1>
    <table class="table table-sm table-dark" style={{textAlign:'center'}}>
  <thead>
    <tr>
      <th scope="col">Location</th>
      <th scope="col">Confirmed Case Indian</th>
      <th scope="col">Confirmed Case Foreign</th>
      <th scope="col">Discharge</th>
      <th scope="col">Deaths</th>
      <th scope="col">Total Confirmed</th>
    </tr>
  </thead>
        <tbody>
        {
            covidData.map((getDetail)=>{
                return(
                    
                            <tr>
                                <td>{getDetail.loc}</td>
                                <td>{getDetail.confirmedCasesIndian}</td>
                                <td>{getDetail.confirmedCasesForeign}</td>
                                <td>{getDetail.discharged}</td>
                                <td>{getDetail.deaths}</td>
                                <td>{getDetail.totalConfirmed}</td>
                            </tr>       
                )
            })
        }
        </tbody>
</table>
    </div>
    <div className="row">
        <div className="col-lg-4 s">
        <img src="/images/covid1.jpg" style={{width:'400px'}}/>
        </div>
        <div className="col-lg-4 ">
        <img src="/images/covid2.png" style={{width:'400px',height:'400px'}}/>
        </div>
        <div className="col-lg-4 ">
        <img src="/images/covid3.jpg" style={{width:'400px',height:'400px'}}/>
        </div>
    </div>
    </>
)
}

export default Covid