import React, {useEffect , useState} from 'react';
import axios from 'axios'

function News(props){
    
    const [newsData , setNewsData] = useState([]);

    const url = 'https://newsapi.org/v2/top-headlines?country=in&apiKey=c7b2f0f57c364e6f9bed11d164d7f5f1';

    const getNews = async() => {
        console.log('axios is called')
    const response = await axios.get(url);
    setNewsData((await response.data).articles);
}

useEffect(() => {
getNews();
},[]);

return (
    <div>
    {
       newsData.slice(props.start,props.end).map((newsItem) => {
            return(
              <div className="newsBox col-12" style={{marginBottom:'5px'}}>
              <img src={newsItem.urlToImage?newsItem.urlToImage:"https://i.redd.it/fr3ij4z9gti01.jpg"}
               style={{width:'100%',height:'80%'}}
               />
              <a href={newsItem.url} style={{fontSize:'18px',textAlign:'center',color:'black'}}>{newsItem.title}</a>
              </div>
            )
        })
    }
    </div>
)
}
export default News