import React,{ useEffect , useState }from 'react';

function ForeCast(){
   const key = process.env.REACT_APP_WEATHER_API;
   const [mainTemp,setMainTemp] = useState('');
   const [description,setDescription] = useState('');
   const [main,setMain] = useState('');
   const [iconID,setIconID] = useState('');


useEffect(()=> {
fetch('https://api.openweathermap.org/data/2.5/weather?q=chhattisgarh,India&APPID=' +key+ '&units=metric')
.then(res=>res.json())
.then(data=>{
   setMainTemp(data.main.temp);
   setDescription(data.weather[0].description);
   setMain(data.weather[0].main);
   setIconID(data.weather[0].icon);
 })
},[])
return (
   <div className="newsBox1" style={{width:'100%',textAlign:'center'}}>
   <h3>Weather Forecast</h3>
   <hr/>
   <p>Temprature : {mainTemp} degree celcius</p>
   <p>Weather Parameters : {main}</p>
   <p>Description : {description}</p>
   <img src ={"https:openweathermap.org/img/wn/" + iconID +"@2x.png"}/>
   <img src ={"https:openweathermap.org/img/wn/" + iconID +"@2x.png"}/>
   </div>
)
}

export default ForeCast;
