import React, {useEffect , useState} from 'react';
import Modal from 'react-bootstrap/Modal';

function TopNews(props){
    
    const [newsData , setNewsData] = useState([]);

    const url = 'https://newsapi.org/v2/top-headlines?country=in&apiKey=c7b2f0f57c364e6f9bed11d164d7f5f1';

    const getNews = async() => {
    const response = await fetch(url);
    setNewsData((await response.json()).articles);
}

useEffect(() => {
getNews();
},[]);

return (
    <div class="card" style={{width: '18rem'}}>
    <div class="card-header overflow-auto" style={{textAlign:'center'}}>
      Top Stories
    </div>
    {
       newsData.slice(props.start,props.end).map((newsItem) => {
            return(
            <ul class="list-group list-group-flush">
            <a href={newsItem.url}><li class="list-group-item">{newsItem.title}</li></a>
            </ul>
            )
        })
    }
    </div> 
)
}
export default TopNews



