import React, {useEffect , useState} from 'react';
import Card from "./Card"


function Categorynews(props){

    const [newsData , setNewsData] = useState([]);
    const category = '&category='+props.Type;
    const url = 'https://newsapi.org/v2/top-headlines?country=in'+category+'&apiKey=c7b2f0f57c364e6f9bed11d164d7f5f1';

    const getNews = async() => {
    const response = await fetch(url);
    setNewsData((await response.json()).articles);
}

useEffect(() => {
getNews();
},[]);

return (
    <div className="row">
    {
       newsData.map((newsItem)=>{
        return(
            <Card
           Img = {newsItem.urlToImage}
           Title = {newsItem.title}
           Desc = {newsItem.description}
           Url = {newsItem.url}
           cardSize={props.flag?"":"590px"}
       />
        )
       })
    }
    </div>
)
}
export default Categorynews
