import React, {useState} from 'react';
import {NavLink , useHistory} from 'react-router-dom';
import './login.css';

function Login(){

    const history = useHistory();

    const [Login,setLogin] = useState({
        username: "",
        password: ""
    })

    let name,value;

    function handleChange(event){
        name = event.target.name;
        value = event.target.value;

        setLogin({...Login,[name]:value});
    }

    const postData = async (e) => {
        e.preventDefault();
        const {username,password} = Login;
        console.log('credentials is true')

        const res = await fetch("https://newsnow85.herokuapp.com/login",{
            method:"POST",
            credentials:"include",
            headers:{'Content-Type': 'application/json; charset=utf-8'},
            body:JSON.stringify(Login)
        })
        const data = await res.json();

        if(res.status === 400 || !data){
            window.alert("Invalid Credentials")
        }
        else{
            window.alert("Login Successfull");
            history.push("/dashboard");
        }
    }

    return (
        <div className="myModal">
        <div className="modal-dialog modal-login">
            <div className="modal-content">
                <div className="modal-header">
                    <h4 className="modal-title">Member Login</h4>
                </div>
                <div className="modal-body">
                    <form  method="POST">
                        <div className="form-group">
                            <i className="fa fa-user"></i>
                            <input type="text" className="form-control" onChange={handleChange} value={Login.email} placeholder="Username" name="username" required="required"/>
                        </div>
                        <div className="form-group">
                            <i className="fa fa-lock"></i>
                            <input type="password" className="form-control" onChange={handleChange} value={Login.password} placeholder="Password" name="password" required="required"/>
                        </div>
                        <div className="form-group">
                            <input type="submit" onClick={postData} className="btn btn-primary btn-block btn-lg" value="Login"/>
                        </div>
                    </form>
    
                </div>
                <div className="modal-footer">
                <NavLink exact activeClassName="active_class" to="/register" >Need Account?</NavLink>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Login;
